@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
:root{
  /*PRIMARY*/
  --strong-cyan: hsl(172, 67%, 45%);
  /*NEUTRAL*/
  --very-dark-cyan: hsl(183, 100%, 15%);
  --dark-grayish-cyan-a: hsl(186, 14%, 43%);
  --dark-grayish-cyan-b: hsl(184, 14%, 56%);
  --light-grayish-cyan-a: hsl(185, 41%, 84%);
  --light-grayish-cyan-b: hsl(189, 41%, 97%);
  --white: hsl(0, 0%, 100%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Space Mono', monospace;
}

img {
  display: block;
  max-width: 100%;
}
body {
  background-color: var(--light-grayish-cyan-a);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 32px;
}

.Home_home__2JYF1 > img {
  display: block;
  margin: 0 auto 32px auto;
}
.Calculator_container__QQh5f {
  background-color: var(--white);
  width: 100vw;
  max-width: 408px;
  border-radius: 16px 16px 0 0;
  padding: 24px;
}

@media (min-width: 900px){
  .Calculator_container__QQh5f {
    max-width: 790px;
    display: flex;
    border-radius: 16px;
  }
}
.Input_input__3NBf5 {
  display: flex;
  flex-direction: column;
}

